import { inject, injectable } from "inversify";
import { TYPES } from "../types";

@injectable()
export abstract class FS<T1 = any, T2 = any, T3 = any> {
  // @inject(TYPES.DbClient) protected abstract conn: T2
  constructor(@inject(TYPES.DbClient) protected connection: T2) { }

  protected abstract getFileBuffer(filename: string): Promise<Buffer>;
  protected abstract findOne(filename: string): Promise<T1 | null>;
  protected abstract openDownloadStreamByName(filename: string): T3;
  protected abstract delete(id: T3): T3;
}