import express, { Express, Router } from "express";
// import { router } from "../routes";
import { ErrorHandleMiddleware } from "../middleware/errorHandlingMiddleware";
// import { mongoURI, port } from './../../config/default.json'
import { Cors, DB, IFileController, IServer, TYPES } from "../interfaces";
import { Container } from "inversify";
import { InversifyExpressServer } from "inversify-express-utils";
import { mainModule } from "../containerDI";
import cors, { CorsRequest } from 'cors'
import * as http from "http";

export class HttpServer<T, O = any, C = any> implements IServer {
  private app!: InversifyExpressServer
  constructor(
    private framework: typeof express,
    private dataBase: DB<T, O, C>,
    private containerDI: Container
  ) {
    console.log(1)
    this.app = new InversifyExpressServer(containerDI)
    containerDI.load(mainModule)
    console.log(2)
    this.app.setConfig((app) => {
      app.use(cors())
      app.use(this.framework.json())
      app.use(this.framework.urlencoded({ extended: false }))
    })
    console.log(3)
    this.app.setErrorConfig((app) => {
      app.use(ErrorHandleMiddleware)
    })
    console.log(4)
  }

  async connectToDB() {
    try {
      console.log(7)
      await this.dataBase.connect(process.env.mongoURI || '')
      console.log(8)
    } catch (e) {
      if (e instanceof Error) {
        console.log('Server error', e.message)
      }
      process.exit(1)
    }
  }

  useRouter() {
    // this.app.use('/api', this.router)
  }

  listen() {
    console.log(5)
    const build =  this.app.build()
    // this.app.build.
    console.log(6)
    const app = build.listen(process.env.PORT || '3000');
    console.log(`Application listening on port ${process.env.PORT}...`);

    this.containerDI.bind<http.Server>(TYPES.App).toConstantValue(app);
   
  }
}
