
import { User } from "../models/User";
import { updateUser } from "../utils/base";


import { controller, httpPatch, httpGet, httpDelete } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { Friend } from "../models/Friend";
import { Room } from "../models/Room";
import mongoose from "mongoose";
import { myContainer } from "../containerDI";
import { TYPES } from "../interfaces";
import { WsServer } from "../modules/WsServer";

@controller("/api/room", authMiddleWare)
export class RoomController {

  @httpGet('/createRoom')
  async createRoom(request: any, response: any) {

    const { idUser } = request._user;
    // const { candidateFriendId } = request.query;
    
    const room = new Room({
      admin: new mongoose.Types.ObjectId(idUser),
      users: [new mongoose.Types.ObjectId(idUser)],
      messages: []
    }) 

    await room.save()
    
    
    return response.json(room.toObject())
  }

  @httpGet('/getRooms')
  async getRooms(request: any, response: any) {

    const { idUser } = request._user;
    // const { candidateFriendId } = request.query;
    
    const rooms = await Room.find({ "users": new mongoose.Types.ObjectId(idUser) })
    
    return response.json(rooms)
  }

  @httpGet('/setUserInRoom')
  async setUserInRoom(request: any, response: any) {

    const { idUser } = request._user;
    const { friendId } = request.query;
    const { roomId } = request.query;
    
    const updatedRoom = await Room.updateOne({_id: roomId}, {
      "$push": {
          "users": friendId
      }}
    )
    
    
    return response.json(updatedRoom)
  }

  @httpGet('/joinToRoom')
  async joinToRoom(request: any, response: any) {

    const { idUser } = request._user; 
    const { roomID } = request.query;
  
    const ws = myContainer.get<WsServer>(TYPES.WsServer)
    
    ws.joinToRoom(roomID, idUser)

    return response.send('ok')
  }

  @httpDelete('/deleteRoom')
  async deleteRoom(request: any, response: any) {

    const { idUser } = request._user; 
    const { roomID } = request.query;
  
    
    const rooms = await Room.findOneAndDelete({_id: roomID},{ new: true })

    return response.json(rooms)
  }

}


export const roomController = new RoomController()
