import ip from "ip";
// import { Fs, GridFS } from "./gridFS";
import { ApiError } from "../error/ApiError";
import { FileSchema, FS } from "./intefaces";
// import { gridFs } from "./gridFS";
import { NextFunction } from "express";
import 'reflect-metadata';
import { injectable, inject } from "inversify";
import { TYPES } from "./../interfaces/types";
import { IFileController } from "../interfaces";
import { BaseHttpController, controller, httpDelete, httpGet, httpPost } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { upload } from "../middleware/file";

// @injectable()
@controller('/api/file')
export class FileController implements IFileController {
  constructor(@inject(TYPES.Fs) private fs: FS<FileSchema>) { }

  @httpPost('/upload', authMiddleWare, upload.single('file'))
  async uploadPhoto(req: any, res: any) {
    // res.

    if (req.file === undefined) return res.send("you must select a file.");

    const imgUrl = `https://build-wheat-rho.vercel.app/api/file/${req.file.filename}`

    // `${this.URL}/avatar/${req.file.filename}`;
    return res.send(imgUrl);
  }


  @httpGet('/:filename')
  async getPhoto(req: any, res: any, next: NextFunction) {

    try {

      const file: any = await this.fs.findOne(req.params.filename);
      if (file) {
        res.setHeader('content-type', 'application/octet-stream');
        const buffer = await this.fs.getFileBuffer(file.filename)
        return res.send(buffer)
      }
      return next(ApiError.badRequest('photo not a found'))
    } catch (error) {

      return res.send('not found');
    }
  }

  @httpDelete('/:filename', authMiddleWare)
  async deletePhoto(req: any, res: any, next: NextFunction) {
    try {

      const result = await this.fs.findOne(req.params.filename);
      if (result?._id) {
        const remove = this.fs.delete(result._id)
        return res.send("success");
      }
      return next(ApiError.badRequest('photo not a found'))
      // const removeFile = await this.gfs.files.findOneAndDelete({ filename: req.params.filename});
      // const removeChunk = await this.gfs.collection('photos.chunks').findOneAndDelete({files_id: result?._id})

    } catch (error) {
      console.log(error);
      res.send("An error occur.");
    }
  }
}

