
import { Schema, model } from 'mongoose'

export const dictionarySchema = new Schema({
  key: String,
  user: {
    type: Schema.Types.ObjectId,
    ref: 'User'
  },
  value: {
    language: { type: String, required: true },
    translatedTo: { type: String, required: true },
    word: { type: String, required: true },
    themes: {
      type: Object,
      required: false
    },
    otherTranslates: {
      type: Object,
      required: false
    },
    translate: { type: String, required: true },
    synonyms: { type: Object, required: true },
    partsOfSpeech: {
      type: Object,
      required: true
    }
  }
},{ timestamps: true })

export const Dictionary = model('Dictionary', dictionarySchema)
export const StaticDictionary = model('StaticDictionary', dictionarySchema)
export const TranslatedDictionary = model('TranslatedDictionary', dictionarySchema)
export const StaticTranslatedDictionary = model('StaticTranslatedDictionary', dictionarySchema)
