import cors from "cors";
import mongoose from "mongoose";
import { TYPES } from "./types";

export type Cors = (
  req: cors.CorsRequest,
  res: {
    statusCode?: number | undefined;
    setHeader(key: string, value: string): any;
    end(): any;
  },
  next: (err?: any) => any
) => void

export interface DB<T, O = any, C = any> {
  connect: (uri: string, options?: O, callback?: C) => Promise<T>
}

export interface IServer {
  connectToDB: () => Promise<void>;
  listen: () => void;
  useRouter: () => void;
}

export interface IFileController {
  uploadPhoto: (req: any, res: any, next: any) => void
  getPhoto: (req: any, res: any, next: any) => void
  deletePhoto: (req: any, res: any, next: any) => void
}

export type DbClient = mongoose.Connection;

export { TYPES }