import { Container, ContainerModule, interfaces } from "inversify";
import mongoose, { Types } from "mongoose";
// import { Server } from "../../server";
import { FileController } from "../controllers/fileController";
import { GridFS } from "../modules/gridFS";
// import { FS } from "../controllers/intefaces";
import { Cors, DB, DbClient, IFileController, IServer, TYPES } from "../interfaces";
import { TYPE } from "inversify-express-utils";
import { Auth, AuthController, authController } from "../controllers/authController";
import { UserController } from "../controllers/userController";
import { RepetitionController } from "../controllers/repetitionController";
import { DictionariesController } from "../controllers/dictionariesController";
import { FS } from "../interfaces/modules";
import { FriendsController } from "../controllers/friendsController";
import {Server as IOServer} from 'socket.io'
import { WsServer } from "../modules/WsServer";
import { RoomController } from "../controllers/roomController";

const myContainer = new Container();

export const mainModule = new ContainerModule(
  (
    bind: interfaces.Bind,
    _unbind: interfaces.Unbind,
    _isBound: interfaces.IsBound,
    _rebind: interfaces.Rebind,
    _unbindAsync: interfaces.UnbindAsync,
    _onActivation: interfaces.Container["onActivation"],
    _onDeactivation: interfaces.Container["onDeactivation"]
  ) => {

    // registerController(bind, AuthController)

    bind<Auth>(TYPES.Auth).to(AuthController).inSingletonScope()
    bind<UserController>(TYPES.User).to(UserController).inSingletonScope()
    bind<FriendsController>(TYPES.Friend).to(FriendsController).inSingletonScope()
    bind<DictionariesController>(TYPES.Dictionary).to(DictionariesController).inSingletonScope()
    bind<RepetitionController>(TYPES.Repetition).to(RepetitionController).inSingletonScope()
    bind<RoomController>(TYPES.Room).to(RoomController).inSingletonScope()

    bind<DbClient>(TYPES.DbClient).toConstantValue(mongoose.connection)

    bind<typeof IOServer>(TYPES.Ws).toConstantValue(IOServer)
    bind<FS>(TYPES.Fs).to(GridFS).inSingletonScope()
    bind<IFileController>(TYPES.FileController).to(FileController).inSingletonScope()
    // bind<Container>(TYPES.Container).toConstantValue(myContainer)
    bind<WsServer>(TYPES.WsServer).to(WsServer).inSingletonScope()

  }
);


function registerController<T>(
  bind: interfaces.Bind,
  constructor: interfaces.Newable<T>
) {
  bind<T>(TYPE.Controller)
    .to(constructor)
    .whenTargetNamed(constructor.name);
}

export { myContainer }














