import mongoose, { InferId } from 'mongoose';
import GridFsStream, { } from "gridfs-stream";
import {
  FileSchema,
  //  FS
} from '../controllers/intefaces';
import { inject, injectable } from 'inversify';
import { TYPES } from '../interfaces';
import { FS } from './../interfaces/modules'

// @injectable()
export class GridFS extends FS<FileSchema, mongoose.Connection>{
  private fs!: GridFsStream.Grid
  private fsBucket!: any
  // private conn!: any

  constructor(protected connection: mongoose.Connection) {
    super(connection)
    connection.once('open', () => {

      this.fsBucket = new mongoose.mongo.GridFSBucket(connection.db, {
        bucketName: 'photos'
      })

      this.fs = GridFsStream(connection.db, mongoose.mongo);
      this.fs.collection('photos');
    })
  }

  protected openDownloadStreamByName(filename: string) {
    return this.fsBucket.openDownloadStreamByName(filename)
  }

  async findOne(filename: string) {
    return await this.fs.files.findOne<FileSchema>({ filename })
  }

  protected delete(id: InferId<FileSchema>) {
    return this.fsBucket.delete(id)
  }

  protected async getFileBuffer(filename: string) {
    const stream = this.openDownloadStreamByName(filename);
    stream.read();
    
    return new Promise<Buffer>((resolve, reject) => {
      const chunks: any = []
      stream.on('data', (data: any) => {
        chunks.push(data);
      });
      stream.on('end', () => {
        const data = Buffer.concat(chunks);
        

        resolve(data);
      });
      stream.on('error', (err: any) => {
        reject(err);
      });
    });
  }
}

