import mongoose, { Model } from "mongoose";

export async function findFriends<T, T1, T2>(UserModel: Model<T, T1, T2>, FriendModel: Model<{}>, id: string) {
  return UserModel.aggregate([
    {
      "$lookup": {
        "from": FriendModel.collection.name,
        localField: "friends",
        foreignField: "_id",
        "let": { "friends": "$friends" },
        "as": "match",
        "pipeline": [
          {
            "$match": {
              "recipient": new mongoose.Types.ObjectId(id),
              "$expr": { "$in": ["$_id", "$$friends"] }
            }
          }
        ],
      }
    }
  ])
}