
import { Schema, model } from 'mongoose'

export const repetitionSchema = new Schema({
  key: String,
  user: {
    type: Schema.Types.ObjectId,
    ref: 'User'
  },
  value: {
    language: { type: String, required: true },
    translatedTo: { type: String, required: true },
    word: { type: String, required: true },
    themes: {
      type: Object,
      required: false
    },
    otherTranslates: {
      type: Object,
      required: false
    },
    translate: { type: String, required: true },
    synonyms: { type: Object, required: true },
    partsOfSpeech: {
      type: Object,
      required: true
    },
    progress: {
      type: [Object],
      required: true
    },
    repeatDay: {
      type: Number,
      required: true
    },
    countRepeatAtStart: {
      type: Number,
      required: true
    },
    repeatAtStart: {
      type: Boolean,
      required: true
    },
    allMistakes: {
      type: Number,
      required: true
    }
  }
},{ timestamps: true })


export const Repetition = model('Repetition', repetitionSchema)
