import { ApiError } from "../error/ApiError";
import { Dictionary, TranslatedDictionary } from "../models/Dictionary";
import { Repetition } from "../models/Repetitions";
import { User } from "../models/User";
import { deleteDocument, getDataDocument, patchDocument, responseAllCollection, setDocument } from "../utils/base";
import jwt, { JwtPayload } from 'jsonwebtoken'
// import { jwtSecret } from './../../config/default.json'
import { controller, httpPost, httpGet } from "inversify-express-utils";
import { inject, injectable } from "inversify";

export interface Auth {
  login(...any: any): Promise<any>
  registration(...any: any): Promise<any>
  refresh(...any: any): Promise<any>
  getUser(...any: any): Promise<any>
}


@controller("/api/auth")
export class AuthController implements Auth {

  // @inject() public _actorRepository: ActorRepository;

  @httpPost('/login')
  async login(req: any, res: any, next: any) {
    const { password, username } = req.body
    const user = await User.findOne({ username })

    if (!user) {
      return next(ApiError.internal('User not a found'))
    }
    let comparePassword = await user.validPassword(password);
    if (!comparePassword) {
      return next(ApiError.internal('Write correct password'))
    }
    const json = user.toAuthJSON()

    return res.json(json)
  }
  @httpPost('/registration')
  async registration(req: any, res: any, next: any) {
    const { email, password, username } = req.body
    // if (!email || !password) {
    //   return next(ApiError.badRequest(' email  password'))
    // }
    const candidate = await User.findOne({ email })
    if (candidate) {
      return next(ApiError.badRequest('The User now exist'))
    }

    const user = new User({
      username,
      email,
      language: 'en',
      translatedLanguage: 'ua'
    })

    await user.setPassword(password)
    await user.save()
    const json = user.toAuthJSON()

    return res.json(json)
  }

  @httpGet('/getUser/:refreshToken')
  async refresh(req: any, res: any, next: any) {
    try {
      const { refreshToken } = req.params
      const decoded = jwt.verify(refreshToken, process.env.jwtSecret || '') as JwtPayload

      const user = await User.findOne({ _id: decoded?.idUser })
      if (!user) {
        return next(ApiError.internal('User not a found'))
      }

      const json = user.toAuthJSON(refreshToken)

      return res.json(json)
    } catch {
      return next(ApiError.notAuth('Refresh token expired'))
    }
  }

  @httpGet('/refresh/:token')
  async getUser(req: any, res: any, next: any) {

    try {
      const { token } = req.params
      const decoded = jwt.verify(token, process.env.jwtSecret || '')


      if (typeof decoded !== 'string') {
        const user = await User.findOne({ _id: decoded?.idUser })
        if (!user) {
          return next(ApiError.internal('User not a found'))
        }

        const json = user.toAuthJSON(token)

        return res.json(json)
      }
      return next(ApiError.internal('Can\'t decode token'))
    } catch (e) {
      return next(ApiError.notAuth('Token expired'))
    }
  }
}

export const authController = new AuthController()
