
import { User } from "../models/User";
import { updateUser } from "../utils/base";


import { controller, httpPatch, httpGet } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { Friend } from "../models/Friend"; 
import { findFriends } from "../utils/friends";

@controller("/api/user", authMiddleWare)
export class UserController {

  @httpGet('/friends')
  async friends(request: any, response: any) {
    const { idUser } = request._user;
     
    const users = await findFriends(User, Friend, idUser)

    const friends = users.filter(friend => friend.match.length)
    

    return response.send(friends)
  }

  @httpGet('/friendsCandidates')
  async friendsCandidates(request: any, response: any) {
    const { idUser } = request._user;
 
    const users = await findFriends(User, Friend, idUser)
    
    const friends = users.filter(friend => !friend.match.length && friend._id.toString() !== idUser)
    

    return response.send(friends)
  }
 
  @httpPatch('/patchActivityDate')
  async patchActivityDate(request: any, response: any) {

    const { idUser } = request._user;

    const userModel = await User.findOne({ _id: idUser })
    const activityDate = new Date()

    await User.findOneAndUpdate({ _id: idUser }, { ...userModel?.toObject(), activityDate }, { new: true })
    return response.status(204).send()
  }

  @httpPatch('/patchAvatar')
  async patchAvatar(request: any, response: any) {
    const { avatar } = request.body

    const updatedField = {
      avatar
    }

    await updateUser(request, User, updatedField)

    return response.status(204).send()
  }

  @httpPatch('/patchProfilePicture')
  async patchProfilePicture(request: any, response: any) {
    const { profilePicture } = request.body

    const updatedField = {
      profilePicture
    }

    await updateUser(request, User, updatedField)

    return response.status(204).send()
  }

  @httpPatch('/patchTranslatedLanguage')
  async patchLanguage(request: any, response: any) {
    const { translatedLanguage } = request.body

    const updatedField = {
      translatedLanguage
    }

    const data = await updateUser(request, User, updatedField)
    
    return response.status(204).send(data?.toObject().language)
  }

  @httpPatch('/patchLanguage')
  async patchTranslatedLanguage(request: any, response: any) {
    const { language } = request.body

    const updatedField = {
      language
    }

    const data = await updateUser(request, User, updatedField)
    
    return response.status(204).send(data?.toObject().translatedLanguage)
  }
}


