import { controller, httpGet, httpPatch, httpPost } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { Dictionary, TranslatedDictionary } from "../models/Dictionary";
import { Repetition } from "../models/Repetitions";
import { deleteDocument, getDataDocument, patchDocument, responseAllCollection, setDocument } from "../utils/base";

@controller('/api/repetitions', authMiddleWare) 
export class RepetitionController {

  @httpGet('/repetitions')
  async getRepetitions(request: any, response: any) {
 
    return await responseAllCollection(Repetition, response, request, true)
  }

  @httpPatch('/patchRepetition/:id')
  async patchRepetition(request: any, response: any) {
    const {id, oldValue} = await getDataDocument(request, Repetition)

   return await patchDocument(Repetition, id, oldValue, request, response)
  }
   
  @httpPost('/setRepetition')
  async setRepetition(request: any, response: any) {
    
    return await setDocument(Repetition, request, response)
  }
  
}

export const repetitionController = new RepetitionController()
