import { controller, httpDelete, httpGet, httpPatch, httpPost } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { Dictionary, StaticDictionary, StaticTranslatedDictionary, TranslatedDictionary } from "../models/Dictionary";
import { deleteDocument, getDataDocument, patchDocument, responseAllCollection, setDocument } from "../utils/base";

@controller('/api/dictionaries', authMiddleWare)
export class DictionariesController {

  @httpDelete('/dictionary/:id')
  async deleteWord(request: any, response: any) {
    const { id } = request.params;
    const { idUser } = request._user;
    
    return deleteDocument(Dictionary, id, response, idUser)
  }

  @httpDelete('/translatedDictionary/:id')
  async deleteTranslatedWord(request: any, response: any) {
    const { id } = request.params;
    const { idUser } = request._user;

   return  deleteDocument(TranslatedDictionary, id, response, idUser)
  }
  
  @httpGet('/dictionary')
  async getDictionary(request: any, response: any) {
 
    await responseAllCollection(Dictionary, response, request)
  }

  @httpGet('/staticDictionary')
  async getStaticDictionary(request: any, response: any) {
 
    return await responseAllCollection(StaticDictionary, response, request)
  }

  @httpGet('/staticTranslatedDictionary')
  async getStaticTranslatedDictionary(request: any, response: any) {
 
    return await responseAllCollection(StaticTranslatedDictionary, response, request)
  }

  @httpGet('/translatedDictionary')
  async getTranslatedDictionary(request: any, response: any) {
 
    return  await responseAllCollection(TranslatedDictionary, response, request)
  }

  @httpPatch('/patchDictionary/:id')
  async patchDictionary(request: any, response: any) {
    const { id, oldValue } = await getDataDocument(request, Dictionary)

    return await patchDocument(Dictionary, id, oldValue, request, response)
  }


  @httpPatch('/patchStaticDictionary/:id')
  async patchStaticDictionary(request: any, response: any) {
    const { id, oldValue } = await getDataDocument(request, StaticDictionary)

    return await patchDocument(StaticDictionary, id, oldValue, request, response)
  }


  @httpPatch('/patchTranslatedDictionary/:id')
  async patchTranslatedDictionary(request: any, response: any) {
    const { id, oldValue } = await getDataDocument(request, TranslatedDictionary)

    return await patchDocument(TranslatedDictionary, id, oldValue, request, response)
  }


  @httpPatch('/patchStaticTranslatedDictionary/:id')
  async patchStaticTranslatedDictionary(request: any, response: any) {
    const { id, oldValue } = await getDataDocument(request, StaticTranslatedDictionary)

    return await patchDocument(StaticTranslatedDictionary, id, oldValue, request, response)
  }

  @httpPost('/setDictionary')
  async setDictionary(request: any, response: any) {
   return  await setDocument(Dictionary, request, response)
  }


  @httpPost('/setTranslatedDictionary')
  async setTranslatedDictionary(request: any, response: any) {
    return await setDocument(TranslatedDictionary, request, response)
  }


  @httpPost('/setStaticDictionary')
  async setStaticDictionary(request: any, response: any) {
    return await setDocument(StaticDictionary, request, response)
  }

  @httpPost('/setStaticTranslatedDictionary')
  async setStaticTranslatedDictionary(request: any, response: any) {
    return await setDocument(StaticTranslatedDictionary, request, response)
  }
}

export const dictionariesController = new DictionariesController()
