import "reflect-metadata";
import express, { Express } from "express";

import ip from "ip";
import mongoose, { Model } from 'mongoose'
import { HttpServer } from "./modules/HttpServer";
import { myContainer } from "./containerDI"; 

console.log('local ip my computer: ', ip.address());
 

async function start() {
  
  const server = new HttpServer<typeof mongoose>(express, mongoose, myContainer)
  console.log('used dependency injection')

  server.listen()
  await server.connectToDB()
  
}

(async () => {

  await start();
})();

