import { Container, inject, injectable } from "inversify";
import * as http from "http";
// import * as socketIo from 'socket.io'
import * as socketIo from 'socket.io';
import { TYPES } from "../interfaces";
import { myContainer } from "../containerDI";
import { Room } from "../models/Room";
import mongoose from "mongoose";


@injectable()
export class WsServer {
  private server!: socketIo.Server
  private app!: http.Server

  constructor(@inject(TYPES.Ws) private ws: typeof socketIo.Server) {
    const app: http.Server = myContainer.get(TYPES.App)
    
    this.app = app
    this.server = new ws(app, {
      // Socket.IO options

    });
 
  }

 

  public joinToRoom(roomID: string, userID: string) {
    // this.server.on('connection', (socket) => {
    //   // socket.emit('news', { hello: 'world' });
    //   socket.broadcast.emit('chat');
    //   
    //   // socket.on('news', function (data: any) {
    //   //   socket.emit('news', data);
    //   // });
    // });
    this.server.once('connection', async (socket) => {
      
      console.log(`${userID} user connected`);
      // const idRoom = ''
      const roomMsg = await Room.findOne({ _id: roomID })
      this.server.emit(`chat:${roomID}:created`, roomMsg?.toObject().messages)
      socket.on(`chat:${roomID}`, async (msg) => {
        const newMsg = {
          msg,
          author: new mongoose.Types.ObjectId(userID),
          date: new Date(),
        }
        await Room.updateOne({ _id: roomID }, {
          "$push": {
            "messages": newMsg
          }
        })
        
        this.server.emit(`chat:${roomID}`, newMsg);
      });
  
      socket.on('disconnect', () => { 
        console.log(`${userID} user disconnected`);
        this.server.emit(`disconnect:${roomID}`, `${userID} user disconnected`); 
      });
    });
  }

}
