import mongoose, { Model } from "mongoose";
import { UserSchema } from "../models/User";


export function parseDictionaryCollection(dictionary: any) {


  const res = Object.values(dictionary).map(parseDocument)

  // const resToObj = res.reduce(arrayToObject, {})
  // 
  // await writeFile(`${PATH_DATA}repetitions.json`, JSON.stringify(resToObj, null, 2));
  return res.reduce((acc, val) => ({ ...acc, [val.word]: val }), {});
}

export function parseDocument(model: any) {

  try {
    return {...model.get('value'), _id: model.get('_id')}
  } catch (e) {
    console.log(e)
  }
}

export async function deleteDocument(Model: Model<{}>, id: string, response: any, user: string) {
  try {

    const doc = await Model.findOneAndRemove({ _id: id, user })
    return response.send(parseDocument(doc));
  } catch (err) {
    return console.log(err);
  } 
}

export const responseAllCollection = async (Model: Model<{}>, response: any, request: any, repetition = false) => {
  const { idUser } = request._user;
  const {language, translatedLanguage} = request.query
 
  try {
    let collection
    if (repetition) {
      collection = await Model.find({ user: idUser, 'value.language': [language, translatedLanguage], 'value.translatedTo': [language, translatedLanguage] })
        // .find({'value.language': translatedLanguage, 'value.translatedTo': language })
    } else {
      collection = await Model.find({ user: idUser, 'value.language': language, 'value.translatedTo': translatedLanguage })

    }
    
    return response.send(parseDictionaryCollection(collection))
  } catch (err) {
    return console.log(err);
  }

}

export async function patchDocument(Model: Model<{}>, id: string, oldValue: any, request: any, response: any) {
  const { idUser } = request._user;

  try {
    const res = await Model.findOneAndUpdate({ _id: id, user: idUser }, { value: { ...oldValue, ...request.body } }, { new: true });
   return  response.send(parseDocument(res));
  } catch(err) {
    return console.log(err);
  }

}

export async function getDataDocument<T>(request: any, Model: Model<T>) {
  const { id } = request.params;
  const { idUser } = request._user;

  const oldWord = await Model.findOne({ _id: id, user: idUser })
  const oldValue = oldWord?.toObject().value || {}

  return { id, oldValue }
}

export async function setDocument(Model: Model<{}>, request: any, response: any) {
  const { idUser } = request._user;

  const documentAlreadyExist = await Model.findOne({ key: request.body.word, user: idUser })

  if (documentAlreadyExist) {
    return response.status(404).send()
  }

  const document = new Model({ key: request.body.word, value: request.body, user: new mongoose.Types.ObjectId(idUser) });

 
  try {
    await document.save()
    return response.send(parseDocument(document));
  } catch (error) {
    return console.log(error);
  }
  
}

export async function updateUser(request: any, Model: Model<UserSchema>, updatedField: {[key: string]: string}) {

  const { idUser } = request._user;
  const userModel = await Model.findOne({ _id: idUser })
 
  
  return await Model.findOneAndUpdate({ _id: idUser }, { ...userModel?.toObject(), ...updatedField}, { new: true })
} 