
import { Schema, model, Types } from 'mongoose'
import bcrypt from 'bcryptjs'
import jwt, { JwtPayload } from 'jsonwebtoken'
// import { jwtSecret } from './../../config/default.json'

export const userSchema = new Schema<UserSchema, {}, IUserMethods>({
  username: { type: String, lowercase: true, unique: true, required: [true, "can't be blank"], match: [/^[a-zA-Z0-9]+$/, 'is invalid'], index: true },
  email: { type: String, lowercase: true, unique: true, required: [true, "can't be blank"], match: [/\S+@\S+\.\S+/, 'is invalid'], index: true },
  isProAccount: { type: Boolean, default: false },
  password: String,
  profilePicture: String,
  avatar: String,
  biography: String,
  gender: { type: String, enum: ["male", "female"] },
  birthday: Number,
  hobbies: String,
  activityDate: Date,
  language: {
    type: String,
    required: true
  },
  translatedLanguage: {
    type: String,
    required: true
  },
  transactions: [{
    date: Number,
    price: Number,
    currency: String,
    exp: Number,
  }],
  friends: [{ type: Schema.Types.ObjectId, ref: 'Friends'}]
}, { timestamps: true })

export type UserSchema = {
  _id?: Types.ObjectId;
  username: string;
  email: string;
  isProAccount?: boolean;
  password?: string;
  profilePicture?: string;
  avatar: string;
  biography?: string;
  gender?: 'male' | 'female';
  birthday?: number;
  hobbies?: string;
  activityDate?: Date;
  language: string;
  translatedLanguage: string;
  transactions?: {
    date: number;
    price: number;
    currency: string;
    exp: number;
  }[],
  friends: string[]
}

interface IUser {
  username: string;
  email: string;
  isProAccount: boolean;
  token: string;
  refreshToken: string;
}

interface IUserMethods {
  setPassword(password: string): Promise<void>;
  validPassword(password: string): Promise<boolean>;
  generateJWT(): string;
  generateJWRT(token: string): string;
  toAuthJSON(tokenOuter?: string): IUser
}
//mongo88
userSchema.methods.setPassword = async function (password: string) {
  const hashedPassword = await bcrypt.hash(password, 12)
  this.password = hashedPassword
}

userSchema.methods.validPassword = async function (password: string): Promise<boolean> {
  const isMatch = await bcrypt.compare(password, this.password)
  return isMatch
}

userSchema.methods.generateJWT = function () {
  const token = jwt.sign(
    {
      idUser: this._id,
    },
    process.env.jwtSecret || '',
    {
      expiresIn: '1d',
      jwtid: '1'
    }
  )

  return token
}

userSchema.methods.generateJWRT = function refresh(token: string) {
  // const token = this.toAuthJSON().token
  const payload = jwt.decode(token) as JwtPayload;


  delete payload.exp;
  delete payload.iat;
  delete payload.jti
  return jwt.sign(payload, process.env.jwtSecret || '', { expiresIn: '2d', jwtid: '2' });

  // delete payload.nbf;
  // delete payload.jti; //We are generating a new token, if you are using jwtid during signing, pass it in refreshOptions
  // const localJwtSignOptions = Object.assign({}, { jwtid: refreshOptions.jwtid }, {expiresIn: '1h'});
  // The first signing converted all needed options into claims, they are already in the payload
}

userSchema.methods.toAuthJSON = function (oldToken?: string) {
  const { username, email, isProAccount, _id, avatar, profilePicture, language, translatedLanguage } = this
  const token = oldToken || this.generateJWT();
  return {
    username,
    email,
    isProAccount,
    token,
    avatar,
    profilePicture,
    refreshToken: this.generateJWRT(token),
    idUser: _id,
    language,
    translatedLanguage
  }
}

export const User = model('User', userSchema)
