
import { User } from "../models/User";
import { updateUser } from "../utils/base";


import { controller, httpPatch, httpGet } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { Friend } from "../models/Friend";

@controller("/api/friends", authMiddleWare)
export class FriendsController {

  @httpGet('/requestBeFriend')
  async requestBeFriend(request: any, response: any) {

    const { idUser } = request._user;
    const { candidateFriendId } = request.query;
    
    const docA = await Friend.findOneAndUpdate(
      { requester: idUser, recipient: candidateFriendId },
      { $set: { status: 1 } },
      { upsert: true, new: true }
    )
    const docB = await Friend.findOneAndUpdate(
      { recipient: idUser, requester: candidateFriendId },
      { $set: { status: 2 } },
      { upsert: true, new: true }
    )
    const updateUserA = await User.findOneAndUpdate(
      { _id: idUser },
      { $push: { friends: docA._id } }
    )
    const updateUserB = await User.findOneAndUpdate(
      { _id: candidateFriendId },
      { $push: { friends: docB._id } }
    )
    
    return response.status(204).send()
  }

  @httpGet('/accept')
  async accept(request: any, response: any) {
    const { idUser } = request._user;
    const { candidateFriendId } = request.query;

    await Friend.findOneAndUpdate(
      { requester: idUser, recipient: candidateFriendId },
      { $set: { status: 3 } }
    )
    await Friend.findOneAndUpdate(
      { recipient: idUser, requester: candidateFriendId },
      { $set: { status: 3 } }
    )

    return response.status(204).send()
  }

  @httpGet('/reject')
  async reject(request: any, response: any) {
    const { idUser } = request._user;
    const { candidateFriendId } = request.query;

    const docA = await Friend.findOneAndRemove(
      { requester: idUser, recipient: candidateFriendId }
    )
    const docB = await Friend.findOneAndRemove(
      { recipient: idUser, requester: candidateFriendId }
    )

    if (docA?._id && docB?._id) {
      
      const updateUserA = await User.findOneAndUpdate(
        { _id: idUser },
        { $pull: { friends: docA._id } }
      )
      const updateUserB = await User.findOneAndUpdate(
        { _id: candidateFriendId },
        { $pull: { friends: docB._id } }
      )

      return response.status(204).send()
    }

    
    return response.status(500).send()
  }

}


export const friendsController = new FriendsController()
